@import "../abstracts/mixins.scss";

.about-container {
    max-width: 1400px;
    margin: 0 auto;
    height: 80vh;
    @include flexbox;

    .about-title-container {
        @include flexbox-c-c;
        flex: 0.65;
        height: 100%;
    }

    .about-bio-container {
        flex: 1;
        margin: auto 4rem;
        padding: 10rem 0;
        width: 40%;

        h3 {
            margin-bottom: 2rem;
        }

        .skills {
            margin-top: 2rem;
        }

        .skill-icons-container {

            // height: 15rem;

            ul {
                height: 100%;
                @include flexbox-sa-sa;

                li {
                    height: 8rem;
                    width: 8rem;
                   

                    @include flexbox-c-c;

                    i  {
                        font-size: 8rem;
                        
                    }

                    .fa-brands {
                        color: black;
                    }
                }
            }
        }
    }

    .line {
        margin: auto 0;
        width: 2px;
        height: 60%;
        background-color: $secondaryCol;
        position: relative;
    }
}